import { useEffect } from 'react';
import React, { useRef } from 'react';

export interface CanvasType extends React.DetailedHTMLProps<React.CanvasHTMLAttributes<HTMLCanvasElement>, HTMLCanvasElement> {
    width?: string;
    height?: string;
}

export const Canvas: React.FC<CanvasType> = (props) => {
    const { children } = props;
    const canvasRef = useRef<any>('');

    useEffect(() => {
        const canvas: HTMLCanvasElement = canvasRef.current;
        const ctx: any = canvas.getContext('2d');
    }, []);
    return (
        <>
            <canvas id="myCanvas" style={{ width: '100vw', height: '100vh' }} ref={canvasRef} {...props}></canvas>
            <section style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>{children}</section>
        </>
    );
};
